// Home Page
.home {
  height: 100%;
  overflow: hidden;
  position: relative;
  background-image: linear-gradient(135deg, rgb(213, 213, 213) 20%, rgb(174, 174, 174));
  section {
    height: 100%;
    // position: relative;
  }
  &-mantile {
    width: 100%;
    height: 100%;
    background-image: url('../../static/bg-s.svg');
  }
  &-begin {
    &-header {
      &-init {
        position: relative;
        margin: auto;
        height: 49.5vh;
        border-radius: 0;
        top: 0vh;
        box-sizing: border-box;
        background-image: linear-gradient(to bottom, rgb(248, 248, 248), rgb(38, 3, 3));
        transition: top .5s ease;
      }
      &-open {
        top: -50vh;
        background: rgb(60, 60, 60);
      }
    }
    &-main {
      position: absolute;
      width: 20vh;
      height: 20vh;
      background: rgb(0, 0, 0);
      z-index: 2;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      margin: auto;
      transform: rotate(45deg);
      border-radius: 5px;
      display: flex;
      .s1 {
        position: absolute;
        top: -10vh;
        left: 0;
        bottom: 0;
        right: 0;
        margin: auto;
        width: 4vh;
        height: 4vh;
        transform: rotate(45deg);
        background: #000;
        border-radius: 10% 20px 10% 20px;
      }
      .s2 {
        position: absolute;
        top: 0;
        left: -10vh;
        bottom: 0;
        right: 0;
        margin: auto;
        width: 4vh;
        height: 4vh;
        transform: rotate(-45deg);
        background: #000;
        border-radius: 10% 20px 10% 20px;
      }
      .s3 {
        position: absolute;
        top: 0;
        left: 0;
        bottom: 0;
        right: -10vh;
        margin: auto;
        width: 4vh;
        height: 4vh;
        transform: rotate(-45deg);
        background: #000;
        border-radius: 10% 20px 10% 20px;
      }
      .s4 {
        position: absolute;
        top: 0;
        left: 0;
        bottom: -10vh;
        right: 0;
        margin: auto;
        width: 4vh;
        height: 4vh;
        transform: rotate(45deg);
        background: #000;
        border-radius: 10% 20px 10% 20px;
      }
      .s5 {
        position: absolute;
        top: 0;
        left: 0;
        bottom: 0;
        right: 0;
        margin: auto;
        width: 4vh;
        height: 4vh;
        background: #000;
        clip-path: polygon(0 20%, 20% 0, 80% 100%,100% 80%)
      }
      .s6 {
        position: absolute;
        top: 0;
        left: 0;
        bottom: 0;
        right: 0;
        margin: auto;
        width: 4vh;
        height: 4vh;
        background: #000;
        clip-path: polygon(80% 0%, 100% 20%, 0% 80%,20% 100%)
      }
      &-wrap {
        position: relative;
        height: 16vh;
        width: 16vh;
        margin: 2vh;
        // box-shadow: 0 0 15px 10px rgb(255, 255, 255);
      }
    }
    &-footer {
      &-init {
        position: relative;
        height: 49.5vh;
        bottom: -1vh;
        background: #000;
        transition: bottom .5s ease;
      }
      &-open {
        bottom: -50.5vh;
        background: rgb(60, 60, 60);
      }
    }
  }
  &-desktop {
    &-main {
      position: absolute;
      top: 0;
      bottom: 0;
      right: 0;
      left: 0;
      margin: auto;
      background: #fff;
      width: 80%;
      height: 90vh;
      border-radius: 5px;
      &-ul {
        display: flex;
        flex-wrap: wrap;
        justify-content: space-around;
        &-li {
          margin: 10px;
          width: 15vw;
          min-width: 100px;
          min-height: 132.8px;
          max-width: 150px;
          max-height: 200px;
          height: 20vw;
          cursor: pointer;
          transition: all .2s;
          border-radius: 5px;
          box-shadow: 0 0 6px 2px rgb(221, 221, 221)
        }
        :hover {
          transform: scale(1.05);
          box-shadow: 0 0 10px 0px rgb(221, 221, 221)
        }
      }
    }
  }
}